<template>
  <form class="card auth-card" @submit.prevent="">
    <div class="card-content">
      <img class="logo-image" :style="''" src="@/assets/newImages/logo.png">
      <div class="input-field">
        <label for="email"
          >{{ $localizeFilter('email') }}<span class="required">*</span></label
        >
        <input
          id="email"
          type="text"
          v-model.trim="email"
          :class="{ invalid: v$.email.$error }"
        />
        <small
          class="error"
          v-for="(error, index) of v$.email.$errors"
          :key="index"
        >
          {{ error.$message }}
        </small>
      </div>
      <div class="input-field">
        <label for="password">{{ $localizeFilter('password') }}<span class="required">*</span></label>
        <input
        id="password"
        type="password"
        v-model.trim="password"
        :class="{ invalid: v$.password.$error }"
        />
         <small
          class="error"
          v-for="(error, index) of v$.password.$errors"
          :key="index"
        >
          {{ error.$message }}
        </small>
      </div>
    </div>
    <div class="card-action">
      <div>
        <button class="button-2" id="submitButton" @click.once="submitHandler" :key="buttonKey">
          <p class="button-title">{{ $localizeFilter('login') }}</p>
          <i class="material-icons right">send</i>
        </button>
      </div>

      <p class="additional-action">
        {{ $localizeFilter('no_account') }}
        <router-link :to="(buyCoinTicker ? `/register?buyCoinTicker=${buyCoinTicker}` : '/register')">{{ $localizeFilter('sign_up') }}</router-link>
      </p>
      <p class="additional-action">
        {{ $localizeFilter('forgot_password') }}
        <router-link to="/resetPassword">{{ $localizeFilter('restore_password') }}</router-link>
      </p>
    </div>
  </form>
</template>

<script>
import useVuelidate from "@vuelidate/core";
import { required, email, helpers } from "@vuelidate/validators";
import { defineComponent } from "@vue/composition-api";
import localizeFilter from "@/filters/localize.filter"

export default defineComponent({
  props: {
    buyCoinTicker: {
      type: String,
      default: null
    }
  },
  setup() {
    return { v$: useVuelidate() };
  },
  data() {
    return {
      email: "",
      password: "",
      buttonKey: 1
    };
  },
  validations() {
    return {
      email: {
       required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required),
       email: helpers.withMessage(localizeFilter('field_must_be_valid_email_address'), email),
      },
      password: { required: helpers.withMessage(localizeFilter('field_must_not_be_empty'), required) },
    };
  },
  mounted() {
  },
  methods: {
    async submitHandler() {
      this.v$.$touch();
      if (this.v$.$error) {
        setTimeout(()=>{
          this.buttonKey+=1
        }, 1000)
        return;
      }
      const el = document.getElementById('submitButton'); 
      el.classList.add("button--loading");
      const formData = {
        email: this.email,
        password: this.password,
        buyCoinTicker: this.buyCoinTicker
      }
      try {
        await this.$store.dispatch('login', formData)
        const {scope, email} = await this.$store.dispatch('getProfile')
        setTimeout(()=>{
          el.classList.remove("button--loading")
          if (scope=='not_activated_client') {
            this.$router.push('/confirmEmail?email='+email)
          } else {
            this.$router.push('/cabinet')
          }
        },1000)
      } catch (e) {
        setTimeout(()=>{
          this.$error(e)
          el.classList.remove("button--loading")
          this.buttonKey+=1
        }, 1000)
      }
    },
  },
  computed: {
  },
});
</script>

